import React from "react";
import {
  BrowserRouter as Router,
  Route,
  Switch,
  Redirect,
} from "react-router-dom";
import { AddDataPage } from "../AddDataPage";
import { Camera } from "../Camera";
import { Cameras } from "../Cameras";
import { Content } from "../Content";
import { Header } from "../Header";
import { AuthPage } from "../pages/AuthPage/AuthPage";
import { Promo } from "../Promo";

export const useRoutes = (authorizated: boolean) => {
  if (!authorizated) {
    return (
      <Switch>
        <Route path="/cameras" exact>
          <Cameras />
        </Route>
        <Route path="/pets/add">
          <AddDataPage />
        </Route>
        <Route path="/" exact>
          <Promo />
        </Route>
        <Route path="/pets" exact>
          <Promo />
        </Route>
        <Route path="/cameras/:id" exact>
          <Camera />
        </Route>
        <Route path="/auth" exact>
          <AuthPage />
        </Route>
        <Redirect to="/" />
      </Switch>
    );
  } else {
    return (
      <Switch>
        <Route path="/cameras" exact>
          <Cameras />
        </Route>
        <Route path="/pets/add">
          <AddDataPage />
        </Route>
        <Route path="/" exact>
          <Promo />
        </Route>
        <Route path="/pets" exact>
          <Promo />
        </Route>
        <Route path="/cameras/:id" exact>
          <Camera />
        </Route>
        <Redirect to="/" />
      </Switch>
    );
  }
};
