import React from "react";
import styles from "./Cameras.module.css";
import pleer from "./img/pleer.jpg";
import Iframe from "react-iframe";
import { Navigator } from "../Navigator";
import { Link } from "react-router-dom";

export const Cameras = () => {
  const videos = [
    "http://www.youtube.com/embed/xDMP3i36naA",
    "https://www.youtube.com/embed/WRJKQPLezcU",
    "https://www.youtube.com/embed/sAwZA2A1ST0",
    "https://www.youtube.com/embed/z6EchXyieos",
    "https://www.youtube.com/embed/ytISUDU842g",
    "https://www.youtube.com/embed/mMrcuUX6Ptg",
    "https://www.youtube.com/embed/qvT24IoM6Po",
    "https://www.youtube.com/embed/sjCBZqBf4gU",
    "https://www.youtube.com/embed/biElSmzz35w",
  ];

  return (
    <div className={styles.container}>
      <Navigator />
      <div className={styles.cameras_containter}>
        {videos.map((video) => {
          return (
            <div className={styles.camera_preview} key={video}>
              <Link to="/cameras/:id">
                <Iframe
                  url={video}
                  id="myId"
                  width="100%"
                  height="100%"
                  className="myClassname"
                  display="inline"
                />
              </Link>
            </div>
          );
        })}
      </div>
    </div>
  );
};
